.top{
	height: px2rem(70);
	.top-wrap{
		width: 100%;
		height:px2rem(70);
		background-color: #fff;
		h1{
			font-size: px2rem(32);
			color: #333;
			line-height: px2rem(70);
			text-align: center;
			font-weight: normal;
		}
		position:relative;
	}
}
.icon-left {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1.75rem;
    height: 1.75rem;
    background: url(../images/back.png) center no-repeat;
    background-size: 30% 50%;
    z-index: 2;
    display: block;
}